import React from 'react';
import { HashRouter as Router, Route, Routes } from "react-router-dom";
import HomePage from "./pages/HomePage";
import CTAPage from "./pages/CTAPage";
import ModernResults from "./pages/ModernResults";
import TestimonialAdmin from "./pages/TestimonialAdmin";
import Header from './components/Header';
import Footer from './components/Footer';
import { LanguageProvider } from './context/LanguageContext';

// Layout component to conditionally render Header and Footer
const AppLayout = ({ children }) => {
  return (
    <div className="app min-h-screen flex flex-col bg-gradient-to-b from-gray-900 via-gray-800 to-gray-900">
      <Header />
      <main className="pt-20 flex-grow">
        {children}
      </main>
      <Footer />
    </div>
  );
};

// Admin Layout without header and footer
const AdminLayout = ({ children }) => {
  return (
    <div className="app min-h-screen flex flex-col bg-gradient-to-b from-gray-900 via-gray-800 to-gray-900">
      <main className="flex-grow">
        {children}
      </main>
    </div>
  );
};

function App() {
  return (
    <Router>
      <LanguageProvider>
        <Routes>
          <Route path="/admin/testimonials" element={
            <AdminLayout>
              <TestimonialAdmin />
            </AdminLayout>
          } />
          <Route path="/" element={
            <AppLayout>
              <HomePage />
            </AppLayout>
          } />
          <Route path="/modernresults" element={
            <AppLayout>
              <ModernResults />
            </AppLayout>
          } />
          {/* <Route path="/cta" element={
            <AppLayout>
              <CTAPage />
            </AppLayout>
          } /> */}
        </Routes>
      </LanguageProvider>
    </Router>
  );
}

export default App;
