import React from 'react';
import { Link } from 'react-router-dom';
import { useLanguage } from '../context/LanguageContext';
import { motion } from 'framer-motion';
import gptTradeLogo from '../assets/gpttrade logo.png';

const Footer = () => {
  const { language } = useLanguage();
  const currentYear = new Date().getFullYear();

  const socialLinks = [
    {
      name: 'Facebook',
      href: '#',
      icon: (props) => (
        <svg fill="currentColor" viewBox="0 0 24 24" {...props}>
          <path fillRule="evenodd" d="M22 12c0-5.523-4.477-10-10-10S2 6.477 2 12c0 4.991 3.657 9.128 8.438 9.878v-6.987h-2.54V12h2.54V9.797c0-2.506 1.492-3.89 3.777-3.89 1.094 0 2.238.195 2.238.195v2.46h-1.26c-1.243 0-1.63.771-1.63 1.562V12h2.773l-.443 2.89h-2.33v6.988C18.343 21.128 22 16.991 22 12z" clipRule="evenodd" />
        </svg>
      ),
    },
    {
      name: 'Instagram',
      href: '#',
      icon: (props) => (
        <svg fill="currentColor" viewBox="0 0 24 24" {...props}>
          <path fillRule="evenodd" d="M12.315 2c2.43 0 2.784.013 3.808.06 1.064.049 1.791.218 2.427.465a4.902 4.902 0 011.772 1.153 4.902 4.902 0 011.153 1.772c.247.636.416 1.363.465 2.427.048 1.067.06 1.407.06 4.123v.08c0 2.643-.012 2.987-.06 4.043-.049 1.064-.218 1.791-.465 2.427a4.902 4.902 0 01-1.153 1.772 4.902 4.902 0 01-1.772 1.153c-.636.247-1.363.416-2.427.465-1.067.048-1.407.06-4.123.06h-.08c-2.643 0-2.987-.012-4.043-.06-1.064-.049-1.791-.218-2.427-.465a4.902 4.902 0 01-1.772-1.153 4.902 4.902 0 01-1.153-1.772c-.247-.636-.416-1.363-.465-2.427-.047-1.024-.06-1.379-.06-3.808v-.63c0-2.43.013-2.784.06-3.808.049-1.064.218-1.791.465-2.427a4.902 4.902 0 011.153-1.772A4.902 4.902 0 015.45 2.525c.636-.247 1.363-.416 2.427-.465C8.901 2.013 9.256 2 11.685 2h.63zm-.081 1.802h-.468c-2.456 0-2.784.011-3.807.058-.975.045-1.504.207-1.857.344-.467.182-.8.398-1.15.748-.35.35-.566.683-.748 1.15-.137.353-.3.882-.344 1.857-.047 1.023-.058 1.351-.058 3.807v.468c0 2.456.011 2.784.058 3.807.045.975.207 1.504.344 1.857.182.466.399.8.748 1.15.35.35.683.566 1.15.748.353.137.882.3 1.857.344 1.054.048 1.37.058 4.041.058h.08c2.597 0 2.917-.01 3.96-.058.976-.045 1.505-.207 1.858-.344.466-.182.8-.398 1.15-.748.35-.35.566-.683.748-1.15.137-.353.3-.882.344-1.857.048-1.055.058-1.37.058-4.041v-.08c0-2.597-.01-2.917-.058-3.96-.045-.976-.207-1.505-.344-1.858a3.097 3.097 0 00-.748-1.15 3.098 3.098 0 00-1.15-.748c-.353-.137-.882-.3-1.857-.344-1.023-.047-1.351-.058-3.807-.058zM12 6.865a5.135 5.135 0 110 10.27 5.135 5.135 0 010-10.27zm0 1.802a3.333 3.333 0 100 6.666 3.333 3.333 0 000-6.666zm5.338-3.205a1.2 1.2 0 110 2.4 1.2 1.2 0 010-2.4z" clipRule="evenodd" />
        </svg>
      ),
    },
    {
      name: 'Twitter',
      href: '#',
      icon: (props) => (
        <svg fill="currentColor" viewBox="0 0 24 24" {...props}>
          <path d="M8.29 20.251c7.547 0 11.675-6.253 11.675-11.675 0-.178 0-.355-.012-.53A8.348 8.348 0 0022 5.92a8.19 8.19 0 01-2.357.646 4.118 4.118 0 001.804-2.27 8.224 8.224 0 01-2.605.996 4.107 4.107 0 00-6.993 3.743 11.65 11.65 0 01-8.457-4.287 4.106 4.106 0 001.27 5.477A4.072 4.072 0 012.8 9.713v.052a4.105 4.105 0 003.292 4.022 4.095 4.095 0 01-1.853.07 4.108 4.108 0 003.834 2.85A8.233 8.233 0 012 18.407a11.616 11.616 0 006.29 1.84" />
        </svg>
      ),
    },
    {
      name: 'LinkedIn',
      href: '#',
      icon: (props) => (
        <svg fill="currentColor" viewBox="0 0 24 24" {...props}>
          <path fillRule="evenodd" d="M19 0h-14c-2.761 0-5 2.239-5 5v14c0 2.761 2.239 5 5 5h14c2.762 0 5-2.239 5-5v-14c0-2.761-2.238-5-5-5zm-11 19h-3v-11h3v11zm-1.5-12.268c-.966 0-1.75-.79-1.75-1.764s.784-1.764 1.75-1.764 1.75.79 1.75 1.764-.783 1.764-1.75 1.764zm13.5 12.268h-3v-5.604c0-3.368-4-3.113-4 0v5.604h-3v-11h3v1.765c1.396-2.586 7-2.777 7 2.476v6.759z" clipRule="evenodd" />
        </svg>
      ),
    },
    {
      name: 'Telegram',
      href: '#',
      icon: (props) => (
        <svg fill="currentColor" viewBox="0 0 24 24" {...props}>
          <path d="M11.944 0A12 12 0 0 0 0 12a12 12 0 0 0 12 12 12 12 0 0 0 12-12A12 12 0 0 0 12 0a12 12 0 0 0-.056 0zm4.962 7.224c.1-.002.321.023.325.14a.506.506 0 0 1 .171.325c.016.093.036.306.02.472-.18 1.898-.962 6.502-1.36 8.627-.168.9-.499 1.201-.82 1.23-.696.065-1.225-.46-1.9-.902-1.056-.693-1.653-1.124-2.678-1.8-1.185-.78-.417-1.21.258-1.91.177-.184 3.247-2.977 3.307-3.23.007-.032.014-.15-.056-.212s-.174-.041-.249-.024c-.106.024-1.793 1.14-5.061 3.345-.48.33-.913.49-1.302.48-.428-.008-1.252-.241-1.865-.44-.752-.245-1.349-.374-1.297-.789.027-.216.325-.437.893-.663 3.498-1.524 5.83-2.529 6.998-3.014 3.332-1.386 4.025-1.627 4.476-1.635z"/>
        </svg>
      ),
    },
  ];

  return (
    <footer className="relative">
      {/* Animated gradient border */}
      <div className="absolute top-0 left-0 right-0 h-[2px] bg-gradient-to-r from-transparent via-indigo-500 to-transparent animate-pulse"></div>
      
      <div className="relative">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
          {/* Main content with glass effect cards */}
          <div className="grid grid-cols-1 md:grid-cols-12 gap-4 sm:gap-8 mb-8">
            {/* Logo and description - spans 5 columns */}
            <motion.div 
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5 }}
              className="col-span-1 md:col-span-5 bg-gradient-to-br from-gray-800/80 via-indigo-900/50 to-gray-800/80 backdrop-blur-sm rounded-2xl p-4 sm:p-6 border border-indigo-500/20 shadow-[0_0_15px_rgba(79,70,229,0.1)]"
            >
              <img 
                src={gptTradeLogo} 
                alt="GPTTrade Logo" 
                className="h-10 sm:h-12 w-auto mb-4"
              />
              <p className="text-indigo-200 text-sm leading-relaxed">
                {language === 'ar' 
                  ? 'نحن نقدم حلولاً مبتكرة في مجال التداول باستخدام الذكاء الاصطناعي'
                  : 'Nous proposons des solutions innovantes dans le domaine du trading en utilisant l\'intelligence artificielle'}
              </p>
            </motion.div>

            {/* Quick Links and Contact in a single card - spans 7 columns */}
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: 0.1 }}
              className="col-span-1 md:col-span-7 grid grid-cols-1 sm:grid-cols-2 gap-4 sm:gap-8 bg-gradient-to-br from-gray-800/80 via-indigo-900/50 to-gray-800/80 backdrop-blur-sm rounded-2xl p-4 sm:p-6 border border-indigo-500/20 shadow-[0_0_15px_rgba(59,130,246,0.1)]"
            >
              {/* Quick Links */}
              <div className="w-full">
                <h3 className="text-sm font-semibold text-indigo-300 uppercase tracking-wider mb-4 drop-shadow-lg">
                  {language === 'ar' ? 'روابط سريعة' : 'Liens rapides'}
                </h3>
                <ul className="space-y-3">
                  <li>
                    <Link to="/" className="text-gray-300 hover:text-indigo-300 transition-colors duration-300 text-sm group flex items-center">
                      <span className="transform group-hover:translate-x-1 transition-transform duration-300">
                        {language === 'ar' ? 'الرئيسية' : 'Accueil'}
                      </span>
                    </Link>
                  </li>
                  <li>
                    <Link to="/modernresults" className="text-gray-300 hover:text-indigo-300 transition-colors duration-300 text-sm group flex items-center">
                      <span className="transform group-hover:translate-x-1 transition-transform duration-300">
                        {language === 'ar' ? 'إنجازاتنا' : 'Nos Réalisations'}
                      </span>
                    </Link>
                  </li>
                </ul>
              </div>

              {/* Contact Info */}
              <div className="w-full">
                <h3 className="text-sm font-semibold text-indigo-300 uppercase tracking-wider mb-4 drop-shadow-lg">
                  {language === 'ar' ? 'تواصل معنا' : 'Contact'}
                </h3>
                <ul className="space-y-3">
                  <li className="text-gray-300 text-sm">
                    <a href="mailto:contact@gpttrade.com" className="w-full hover:text-indigo-300 transition-colors duration-300 group flex items-center">
                      <span className="transform group-hover:translate-x-1 transition-transform duration-300 break-all">
                        contact@gpttrade.com
                      </span>
                    </a>
                  </li>
                  <li className="text-gray-300 text-sm">
                    <a href="tel:+212781372018" className="w-full hover:text-indigo-300 transition-colors duration-300 group flex items-center">
                      <span className="transform group-hover:translate-x-1 transition-transform duration-300">
                        +212 7 81 37 20 18
                      </span>
                    </a>
                  </li>
                </ul>
              </div>
            </motion.div>
          </div>

          {/* Social Links and Copyright */}
          <div className="flex flex-col md:flex-row justify-between items-center py-4">
            {/* Social Links with hover effects */}
            <motion.div
              initial={{ opacity: 0, x: -20 }}
              whileInView={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.5 }}
              className="flex flex-wrap justify-center gap-4 md:gap-6 mb-4 md:mb-0"
            >
              {socialLinks.map((item) => (
                <a
                  key={item.name}
                  href={item.href}
                  className="text-gray-400 hover:text-indigo-300 transition-all duration-300 transform hover:scale-110 hover:shadow-[0_0_10px_rgba(129,140,248,0.3)]"
                >
                  <span className="sr-only">{item.name}</span>
                  <item.icon className="h-6 w-6" aria-hidden="true" />
                </a>
              ))}
            </motion.div>

            {/* Copyright text with gradient */}
            <motion.p
              initial={{ opacity: 0, x: 20 }}
              whileInView={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.5 }}
              className="text-sm text-center bg-clip-text text-transparent bg-gradient-to-r from-gray-300 via-indigo-300 to-gray-300"
            >
              © {currentYear} GPTTrade. {language === 'ar' ? 'جميع الحقوق محفوظة' : 'Tous droits réservés'}.
            </motion.p>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
