import React, { useState } from 'react';
import { Link } from "react-router-dom";
import { useLanguage } from '../context/LanguageContext';
import { translations } from '../constants/translations';
import gptTradeLogo from '../assets/gpttrade logo.png';
import { motion } from 'framer-motion';

const Header = () => {
  const { language, toggleLanguage } = useLanguage();
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  const handleLinkClick = () => {
    setIsMobileMenuOpen(false);
  };

  return (
    <header className="fixed top-0 left-0 right-0 z-50">
      {/* Glassmorphism background */}
      <div className="absolute inset-0 bg-gray-900/80 backdrop-blur-lg shadow-lg"></div>
      
      <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between h-20">
          {/* Logo */}
          <motion.div
            initial={{ opacity: 0, x: -20 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.5 }}
          >
            <Link to="/" className="flex items-center" onClick={handleLinkClick}>
              <img 
                src={gptTradeLogo} 
                alt="GPTTrade Logo" 
                className="h-16 w-auto object-contain"
              />
            </Link>
          </motion.div>

          {/* Desktop Navigation */}
          <motion.nav
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: 0.1 }}
            className="hidden md:flex items-center space-x-8"
          >
            <Link 
              to="/" 
              className="text-white hover:text-blue-400 transition-colors duration-300 text-sm font-medium"
            >
              {translations[language].home}
            </Link>
            <Link 
              to="/modernresults" 
              className="text-white hover:text-blue-400 transition-colors duration-300 text-sm font-medium"
            >
              {language === 'ar' ? 'إنجازاتنا' : 'Nos Réalisations'}
            </Link>
            <Link 
              to="/admin/testimonials" 
              className="text-white hover:text-blue-400 transition-colors duration-300 text-sm font-medium"
            >
              {language === 'ar' ? 'إدارة الشهادات' : 'Gestion des Témoignages'}
            </Link>
          </motion.nav>

          {/* Language Toggle */}
          <motion.div
            initial={{ opacity: 0, x: 20 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.5 }}
            className="flex items-center space-x-4"
          >
            <button
              onClick={toggleLanguage}
              className="px-4 py-2 bg-gradient-to-r from-blue-600 to-purple-600 text-white rounded-full transition-all duration-300 hover:from-blue-700 hover:to-purple-700 focus:outline-none focus:ring-2 focus:ring-blue-500 text-sm font-medium shadow-lg hover:shadow-xl"
            >
              {translations[language].languageToggle}
            </button>

            {/* Mobile menu button */}
            <div className="md:hidden">
              <button 
                onClick={toggleMobileMenu}
                className="p-2 rounded-full bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 transition-colors duration-300 focus:outline-none focus:ring-2 focus:ring-blue-500 shadow-md"
                aria-label="Toggle mobile menu"
              >
                <svg 
                  className="h-6 w-6 text-white" 
                  fill="none" 
                  viewBox="0 0 24 24" 
                  stroke="currentColor"
                >
                  {isMobileMenuOpen ? (
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                  ) : (
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
                  )}
                </svg>
              </button>
            </div>
          </motion.div>
        </div>
      </div>
      
      {/* Mobile menu */}
      {isMobileMenuOpen && (
        <motion.div 
          initial={{ opacity: 0, y: -10 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: -10 }}
          transition={{ duration: 0.2 }}
          className="absolute top-full left-0 right-0 md:hidden bg-gray-900/95 backdrop-blur-lg shadow-lg border-t border-gray-800"
        >
          <div className="px-4 py-4 space-y-2">
            <Link 
              to="/" 
              className="block px-4 py-3 rounded-lg text-white hover:bg-gray-800/70 transition-all duration-300 font-medium"
              onClick={handleLinkClick}
            >
              {translations[language].home}
            </Link>
            <Link 
              to="/modernresults" 
              className="block px-4 py-3 rounded-lg text-white hover:bg-gray-800/70 transition-all duration-300 font-medium"
              onClick={handleLinkClick}
            >
              {language === 'ar' ? 'إنجازاتنا' : 'Nos Réalisations'}
            </Link>
            <Link 
              to="/admin/testimonials" 
              className="block px-4 py-3 rounded-lg text-white hover:bg-gray-800/70 transition-all duration-300 font-medium"
              onClick={handleLinkClick}
            >
              {language === 'ar' ? 'إدارة الشهادات' : 'Gestion des Témoignages'}
            </Link>
            <Link 
              to="/#contact" 
              className="block px-4 py-3 rounded-lg text-white hover:bg-gray-800/70 transition-all duration-300 font-medium"
              onClick={handleLinkClick}
            >
              {translations[language].contactUs}
            </Link>
          </div>
        </motion.div>
      )}
    </header>
  );
};

export default Header;
